*,
*::before,
*::after {
    margin: 0;
    padding: 0;
    box-sizing: inherit;
}

h1, h2, h3, h4, h5, h6, p {
    margin: 0;
}

a {
    text-decoration: none;
    display: inline-block;
}

ul,
li {
    list-style: none;
}

html {
    font-size: 62.5%;
    scroll-behavior: smooth;
}

html, body {
    height: 100%;
}

body {
    font-size: 1.6rem;
    font-family: 'Poppins', sans-serif;
    font-weight: 400;
    box-sizing: border-box;
}

body.lock {
    overflow: hidden;
}

.wrapper {
    min-height: 100vh;
    overflow: hidden;

    @include flex(column, center, center);
}

main {
    flex: 1 1 auto; //позволяем увеличиваться контентую часть в секции main, таким образом футер всегда будет прибит к низу
    width: 100%;
    height: 100%;
}

.row {
    display: flex;
    flex-wrap: wrap;
}

::selection {}

//@supports rule to specify an img with .webp extention in case a browser supports it
@supports (-webkit-appearance: none) {
    body {
        background-image: url();
    }
}
