*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

a {
  text-decoration: none;
  display: inline-block;
}

ul,
li {
  list-style: none;
}

html {
  font-size: 62.5%;
  scroll-behavior: smooth;
}

html,
body {
  height: 100%;
}

body {
  font-size: 1.6rem;
  font-family: 'Poppins', sans-serif;
  font-weight: 400;
  box-sizing: border-box;
}

body.lock {
  overflow: hidden;
}

.wrapper {
  min-height: 100vh;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

main {
  flex: 1 1 auto;
  width: 100%;
  height: 100%;
}

.row {
  display: flex;
  flex-wrap: wrap;
}

@supports (-webkit-appearance: none) {
  body {
    background-image: url();
  }
}

.heading-1 {
  font-size: calc(24px + 11 * ((100vw - 320px) / 820));
}

.heading-2 {
  font-size: calc(22px + 6 * ((100vw - 320px) / 820));
}

.heading-3 {
  font-size: calc(16px + 4 * ((100vw - 320px) / 820));
}

