.btn {
  display: inline-block;
  font-weight: 600;
  font-size: 1.4rem;
  color: #ffffff;
  padding: 1rem 1.3rem;
  background-color: #FF9900;
  transition: all .3s ease-in-out;
  border: 1px solid transparent;
}

.btn:hover {
  background-color: transparent;
  border: 1px solid #173A56;
  color: #544837;
}

.btn--watch {
  border: none;
  background-color: transparent;
  color: #FF9900;
  text-decoration: underline;
  margin-left: 0.8rem;
  position: relative;
  display: flex;
  align-items: center;
}

.btn--watch:hover {
  border: none;
}

.btn--watch::before {
  content: "";
  position: absolute;
  background-image: url(../img/hero/triangle.svg);
}

.btn__icon {
  width: 1rem;
  height: 1.4rem;
  margin-right: .9rem;
  fill: #FF9900;
  transition: all .3s ease-in-out;
}

.btn:hover .btn__icon {
  fill: #544837;
}

.cards__row {
  margin: 0 -1.5rem;
}

.cards__item {
  flex: 0 1 33.333%;
  max-width: 33.3333%;
  display: flex;
  padding: 0 1.5rem;
}

.cards__item-inner {
  overflow: hidden;
  border: 1rem;
  border: 1px solid #777777;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  align-items: stretch;
  justify-content: flex-start;
}

.cards__item-inner:hover .cards__pic {
  transform: scale(1.1);
}

.cards__img {
  position: relative;
  overflow: hidden;
}

.cards__pic {
  width: 100%;
  height: auto;
  display: block;
  transition: all .2s ease-in;
}

.cards__info {
  padding: 1rem 1.2rem;
  text-align: center;
  flex: 1 1 auto;
}

.cards__text {
  font-size: calc(14px + 2 * ((100vw - 320px) / 820));
  line-height: 2rem;
}

.cards__btn {
  text-align: center;
}

.cards__link:link,
.cards__link:visited {
  align-self: center;
  display: inline-block;
  text-align: none;
  font-size: calc(14px + 2 * ((100vw - 320px) / 820));
}

