/* Modal 2 */

.navigation {
  position: fixed;
  top: 6.4rem;
  left: -100%;
  width: 100%;
  height: 100%;
  background-color: #FF9900;
  overflow: auto;
  opacity: 1;
  visibility: visible;
  transition: all .4s ease-in-out;
  display: flex;
  flex-direction: column;
  align-items: stretch;
  justify-content: center;
}

.navigation__items {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.navigation__list {
  list-style: none;
  margin-bottom: 2rem;
}

.navigation__list:last-child {
  margin-bottom: 0;
}

.navigation__link:link,
.navigation__link:visited {
  display: inline-block;
  font-size: 2rem;
  color: #2F281E;
  text-transform: uppercase;
  transition: all .3s ease-in-out;
  border-radius: 5px;
  position: relative;
}

.navigation__link:link:hover,
.navigation__link:visited:hover {
  color: #FF9900;
}

.navigation.show {
  left: 0;
  opacity: 1;
  visibility: visible;
}

