.toggle {
  display: none;
  position: relative;
  top: 0.7rem;
  z-index: 5;
  border-radius: 10px;
  cursor: pointer;
  display: block;
}

.toggle__btn {
  position: relative;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.toggle__btn,
.toggle__btn::before,
.toggle__btn::after {
  width: 3rem;
  height: 2px;
  background-color: #2F281E;
  display: inline-block;
}

.toggle__btn::before,
.toggle__btn::after {
  content: "";
  position: absolute;
  left: 0;
  transition: all 0.3s;
}

.toggle__btn::before {
  top: -.8rem;
}

.toggle__btn::after {
  top: .8rem;
}

.toggle__btn.active {
  background-color: transparent;
}

.toggle__btn.active::before {
  transform: rotate(135deg);
  top: 0;
}

.toggle__btn.active::after {
  transform: rotate(-135deg);
  top: 0;
}

