.header {
  position: fixed;
  top: 0px;
  left: 0px;
  right: 0px;
  width: 100%;
  z-index: 3;
  transition: all .3s ease-in;
  background-image: linear-gradient(to bottom right, #FFE598, #FFF4E5);
  padding: 1.6rem 0;
  /*Sign In/Sign Up*/
}

.header__row {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  position: relative;
}

.header__get-started-btn {
  display: inline-block;
  font-weight: 600;
  line-height: 2.4rem;
  color: #2F281E;
  padding: .3rem 0.5rem;
  border: 1px solid #BCD0E5;
  transition: all .3s ease-in-out;
  border-radius: 4px;
  font-size: calc(13px + 3 * ((100vw - 320px) / 820));
}

.header__get-started-btn:hover {
  background-color: #FF9900;
  color: #ffffff;
}

.header__get-started-btn--colored {
  border: 1px solid transparent;
  background-color: #FF9900;
  color: #ffffff;
}

.header__get-started-btn--colored:hover {
  border: 1px solid #BCD0E5;
  background-color: transparent;
  color: #2F281E;
}

.header.sticky {
  background-image: linear-gradient(to bottom right, rgba(255, 229, 152, 0.9), rgba(255, 244, 229, 0.9));
  transition: all .4s ease;
  box-shadow: 0 0.0625rem 0.375rem 0 rgba(0, 0, 0, 0.1);
}

.hero {
  padding-top: 12rem;
  padding-bottom: 2rem;
}

.hero__body {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.hero__details {
  text-align: center;
  margin-bottom: 2rem;
}

.hero__details-title {
  font-size: calc(26px + 18 * ((100vw - 320px) / 820));
  font-weight: 700;
  margin-bottom: 1.5rem;
  color: #2F281E;
}

.hero__details-subtitle {
  font-size: calc(15px + 3 * ((100vw - 320px) / 820));
  line-height: 2.8rem;
  color: #544837;
  margin-bottom: 2rem;
}

.hero__btns {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.hero__img-box {
  width: 27rem;
  height: auto;
}

.hero__img {
  width: 100%;
  object-fit: cover;
}

.container {
  max-width: 115.6rem;
  margin: 0 auto;
  padding: 0 1.6rem;
}

