@media only screen and (min-width: 32.5em) {
  .cards__item {
    flex: 0 1 100%;
    max-width: 100%;
    padding: 1.5rem 0;
  }

  .hero__img-box {
    width: 32rem;
  }
}

@media only screen and (min-width: 48em) {
  .btn__icon {
    width: 1.7rem;
    height: 2rem;
  }

  .logo {
    flex: 0 0 15rem;
  }

  .navigation {
    position: initial;
    opacity: 1;
    visibility: visible;
    overflow-y: hidden;
    background-color: transparent;
    transition: none;
    width: 45%;
    margin-left: auto;
  }

  .navigation__items {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
  }

  .navigation__list {
    margin-right: 2rem;
    margin-bottom: 0rem;
  }

  .navigation__list:last-child {
    margin-right: 0;
  }

  .navigation__link:link,
  .navigation__link:visited {
    font-size: 1.4rem;
    text-transform: none;
  }

  .toggle {
    display: none;
  }

  .header {
    background-image: none;
    padding: 2.1rem 0;
  }

  .header__get-started {
    margin-left: 3rem;
  }

  .header__get-started-btn {
    padding: 1rem 2rem;
    margin-right: 1rem;
  }

  .header__get-started-btn:last-child {
    margin-right: 0;
  }

  .hero {
    padding-top: 11rem;
    padding-bottom: 5rem;
  }

  .hero__body {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
  }

  .hero__details {
    text-align: left;
    margin-bottom: 0rem;
    margin-right: 1rem;
  }

  .hero__details-title {
    max-width: 52rem;
  }

  .hero__details-subtitle {
    max-width: 46rem;
  }

  .hero__btns {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
  }

  .hero__img-box {
    width: 42rem;
  }
}

@media only screen and (min-width: 62em) {
  .btn {
    padding: 1.4rem 4.4rem;
    font-size: 1.6rem;
  }

  .logo {
    flex: 0 0 20rem;
  }

  .navigation {
    width: 32%;
  }

  .header__get-started-btn {
    padding: 1.1rem 2.2rem;
    margin-right: 1.4rem;
  }

  .hero {
    padding-top: 13rem;
    padding-bottom: 9rem;
  }

  .hero__details-title {
    margin-bottom: 3rem;
  }

  .hero__details-subtitle {
    margin-bottom: 4.5rem;
  }

  .hero__img-box {
    width: 60.2rem;
  }
}

@media only screen and (min-width: 75em) {
  .header {
    background-color: transparent;
    padding: 2.4rem 0;
  }

  .hero {
    padding-bottom: 11rem;
  }
}

@media (min-width: 90em) {
  .hero__details-title {
    max-width: 57rem;
  }
}

@media only screen and (min-width: 91em) {
  .container {
    max-width: 120.6rem;
  }
}

@media only screen and (min-width: 103em) {
  .hero__details-title {
    max-width: 60rem;
  }

  .hero__img-box {
    max-width: 73rem;
  }

  .container {
    max-width: 130.6rem;
  }
}