.header {
    position: fixed;
    top: 0px;
    left: 0px;
    right: 0px;
    width: 100%;
    z-index: 3;
    transition: all .3s ease-in;
    background-image: linear-gradient(to bottom right, $color-gradient-1, $color-gradient-2);
    padding: 1.6rem 0;

    @include respond(small-phone) {
        background-image: none;
        padding: 2.1rem 0;
    }


    @include respond(small-desktop) {
        background-color: transparent;
        padding: 2.4rem 0;
    }


    &__row {
        @include flex(row, center, space-between);

        position: relative;
    }

    &__logo {}

    &__navigation {}

    /*Sign In/Sign Up*/
    &__get-started {
        @include respond(small-phone) {
            margin-left: 3rem;
        }
    }

    &__get-started-btn {
        display: inline-block;
        font-weight: 600;
        line-height: 2.4rem;
        color: $color-black-magic;
        padding: .3rem 0.5rem;
        border: 1px solid $color-light-blue;
        transition: all .3s ease-in-out;
        border-radius: 4px;

        @include adaptiv-font(16, 13);

        &:hover {
            background-color: $color-orange-peel;
            color: $color-white;
        }

        @include respond(small-phone) {
            padding: 1rem 2rem;
            margin-right: 1rem;

            &:last-child {
                margin-right: 0;
            }
        }


        @include respond(tablet) {
            padding: 1.1rem 2.2rem;
            margin-right: 1.4rem;
        }
    }

    &__get-started-btn--colored {
        border: 1px solid transparent;
        background-color: $color-orange-peel;
        color: $color-white;

        &:hover {
            border: 1px solid $color-light-blue;
            background-color: transparent;
            color: $color-black-magic;
        }
    }
}

//styles for sticky class for header while scrolling down
.header.sticky {
    background-image: linear-gradient(to bottom right, rgba($color-gradient-1, 0.9), rgba($color-gradient-2, 0.9));
    transition: all .4s ease;
    box-shadow: 0 0.0625rem 0.375rem 0 rgba(0, 0, 0, 0.1);
}
