// Mobile First Navigation styles
.navigation {
    position: fixed;
    top: 6.4rem;
    left: -100%;
    width: 100%;
    height: 100%;
    background-color: $color-orange-peel;
    overflow: auto;
    opacity: 1;
    visibility: visible;
    transition: all .4s ease-in-out;

    @include flex(column, stretch, center);

    @include respond(small-phone) {
        position: initial;
        opacity: 1;
        visibility: visible;
        overflow-y: hidden;
        background-color: transparent;
        transition: none;
        width: 45%;
        margin-left: auto;
    }


    @include respond(tablet) {
        width: 32%;
    }


    &__items {
        @include flex(column, center, center);

        @include respond(small-phone) {
            @include flex(row, center, center);
        }
    }

    &__list {
        list-style: none;
        margin-bottom: 2rem;

        @include respond(small-phone) {
            margin-right: 2rem;
            margin-bottom: 0rem;

            &:last-child {
                margin-right: 0;
            }
        }


        &:last-child {
            margin-bottom: 0;
        }
    }

    &__link:link,
    &__link:visited {
        display: inline-block;
        font-size: 2rem;
        color: $color-black-magic;
        text-transform: uppercase;
        transition: all .3s ease-in-out;
        border-radius: 5px;
        position: relative;

        &:hover {
            color: $color-orange-peel;
        }

        @include respond(small-phone) {
            font-size: 1.4rem;
            text-transform: none;
        }
    }
}

//styles for navigation to show and hide on click on toggle-btn
.navigation.show {
    left: 0;
    opacity: 1;
    visibility: visible;
}
