.btn {
    display: inline-block;
    font-weight: 600;
    font-size: 1.4rem;
    color: $color-white;
    padding: 1rem 1.3rem;
    background-color: $color-orange-peel;
    transition: all .3s ease-in-out;
    border: 1px solid transparent;

    @include respond(tablet) {
        padding: 1.4rem 4.4rem;
        font-size: 1.6rem;
    }


    &:hover {
        background-color: transparent;
        border: 1px solid $color-dark-blue;
        color: $color-metal-bronze;
    }

    &--watch {
        border: none;
        background-color: transparent;
        color: $color-orange-peel;
        text-decoration: underline;
        margin-left: 0.8rem;
        position: relative;
        display: flex;
        align-items: center;

        &:hover {
            border: none;
        }

        &::before {
            content: "";
            position: absolute;
            background-image: url(../img/hero/triangle.svg);
        }
    }

    &__icon {
        width: 1rem;
        height: 1.4rem;
        margin-right: .9rem;
        fill: $color-orange-peel;
        transition: all .3s ease-in-out;

        @include respond(small-phone) {
            width: 1.7rem;
            height: 2rem;
        }
    }

    &:hover &__icon {
        fill: $color-metal-bronze;
    }
}
