.hero {
    padding-top: 12rem;
    padding-bottom: 2rem;

    @include respond(small-phone) {
        padding-top: 11rem;
        padding-bottom: 5rem;
    }


    @include respond(tablet) {
        padding-top: 13rem;
        padding-bottom: 9rem;
    }


    @include respond(small-desktop) {
        padding-bottom: 11rem;
    }


    &__body {
        @include flex(column, center, center);

        @include respond(small-phone) {
            @include flex(row, center, space-between);
        }
    }

    &__details {
        text-align: center;
        margin-bottom: 2rem;

        @include respond(small-phone) {
            text-align: left;
            margin-bottom: 0rem;
            margin-right: 1rem;
        }
    }

    &__details-title {
        @include adaptiv-font(44, 26);

        font-weight: 700;
        margin-bottom: 1.5rem;
        color: $color-black-magic;

        @include respond(small-phone) {
            max-width: 52rem;
        }


        @include respond(tablet) {
            margin-bottom: 3rem;
        }


        @media (min-width: 90em) {
            max-width: 57rem;
        }

        @include respond(huge-desktop) {
            max-width: 60rem;
        }
    }

    &__details-subtitle {
        @include adaptiv-font(18, 15);

        line-height: 2.8rem;
        color: $color-metal-bronze;
        margin-bottom: 2rem;

        @include respond(small-phone) {
            max-width: 46rem;
        }


        @include respond(tablet) {
            margin-bottom: 4.5rem;
        }
    }

    &__btns {
        @include flex(row, center, center);

        @include respond(small-phone) {
            @include flex(row, center, flex-start);
        }
    }

    &__img-box {
        width: 27rem;
        height: auto;

        @include respond(tiny-phone) {
            width: 32rem;
        }


        @include respond(small-phone) {
            width: 42rem;
        }


        @include respond(tablet) {
            width: 60.2rem;
        }


        @include respond(huge-desktop) {
            max-width: 73rem;
        }
    }

    &__img {
        width: 100%;
        object-fit: cover;
    }
}
