.toggle {
    display: none;
    position: relative;
    top: 0.7rem;
    z-index: 5;
    border-radius: 10px;
    cursor: pointer;
    display: block;

    @include respond(small-phone) {
        display: none;
    }


    &__btn {
        position: relative;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);

        &,
        &::before,
        &::after {
            width: 3rem;
            height: 2px;
            background-color: $color-black-magic;
            display: inline-block;
        }

        &::before,
        &::after {
            content: "";
            position: absolute;
            left: 0;
            transition: all 0.3s;
        }

        &::before {
            top: -.8rem;
        }

        &::after {
            top: .8rem;
        }
    }
}

//style .show in order to hide or show nav on click to toggle button

.toggle__btn.active {
    background-color: transparent;
}

.toggle__btn.active::before {
    transform: rotate(135deg);
    top: 0;
}

.toggle__btn.active::after {
    transform: rotate(-135deg);
    top: 0;
}
