@mixin font($font_name, $file_name, $weight, $style) {
    @font-face {
        font-family: $font_name;
        font-display: swap;
        src: url("../fonts/#{$file_name}.woff") format("woff"), url("../fonts/#{$file_name}.woff2") format("woff2");
        font-weight: #{$weight};
        font-style: #{$style};
    }
}

//Миксин который добавляет три точки(...) к тексту если его будет больше можно поместить в одну строчку
@mixin textOverflow {
    overflow: hidden;
    -ms-text-overflow: ellipsis;
    text-overflow: ellipsis;
    white-space: nowrap; //чтобы ничего не переносилось на новуб строчку
}

@mixin flex($direction, $align, $justify) {
    display: flex;
    flex-direction: $direction;
    align-items: $align;
    justify-content: $justify;
}

// breakpoints for responsive design - mobile first approach

@mixin respond($breakpoint) {
    @if $breakpoint == tiny-phone {
        @media only screen and (min-width: 32.5em) {
            @content;
        } //min-width: 520px
    }

    @if $breakpoint == small-phone {
        @media only screen and (min-width: 48em) {
            @content;
        } //min-width: 768px
    }

    @if $breakpoint == tablet {
        @media only screen and (min-width: 62em) {
            @content;
        } //min-width: 992px
    }

    @if $breakpoint == small-desktop {
        @media only screen and (min-width: 75em) {
            @content;
        } //min-width: 1200px
    }

    @if $breakpoint == big-desktop { //  big-laptop
        @media only screen and (min-width: 91em) {
            @content;
        } //min-width: 1450px
    }

    @if $breakpoint == huge-desktop { //  big-laptop
        @media only screen and (min-width: 103em) {
            @content;
        } //min-width: 1650px
    }
}

// mixin fro adaptive fonts
$maxWidth: 1140; // 1140px - ширина макета (может быть разная)

@mixin adaptiv-font($pcSize, $mobSize) { // $pcSize -- макетный размер шрифта (допустим 35px) , $mobSize -- минимальный размер шрифта, который необходимо указать при разширеннии экрана - 320px
    $addSize: $pcSize - $mobSize;
    $maxWidth: $maxWidth - 320;

    font-size: calc(#{$mobSize + px} + #{$addSize} * ((100vw - 320px) / #{$maxWidth}));
}

//Documents and Settings

@mixin input-placeholder { // в качестве @content может быть цвет или шрифт (@include input-placeholder {color: $color=black;};
    &.placeholder {
        @content;
    }

    &:-moz-placeholder {
        @content;
    }

    &::-moz-placeholder {
        @content;
    }

    &:-ms-input-placeholder {
        @content;
    }

    &::-webkit-input-placeholder {
        @content;
    }
}

/* 
    Usage of input-placeholder mixin

    @include input-placeholder {
        font-size: 16px;
        color: red
    }
*/

// mixin for responsive images
@mixin responsive-images {
    max-width: 100%;
    height: auto;
    display: block;
}
