.cards {
   
    &__row { //используем отрицательные марджины для того чтобы сделать отступ между каточками (раздвигаем flex container)
        margin: 0 -1.5rem; // на половину отступа по макету (если отступ 30px  то margin-left и margin-right будут -15px)

        @include respond(phone) {
            margin: -1.5rem 0;
            justify-content: center;
        }
    }

    &__item {
        flex: 0 1 33.333%;
        max-width: 33.3333%;
        display: flex;
        padding: 0 1.5rem; //сами колонки (флекс элементы ) сдвигаем при помощи padding на половину отспута по макету

        @include respond(phone) {
            flex: 0 1 50%;
            max-width: 50%;
            padding: 1.5rem 1.5rem;
        }


        @include respond(tiny-phone) {
            flex: 0 1 100%;
            max-width: 100%;
            padding: 1.5rem 0;
        }
    }

    &__item-inner {
        overflow: hidden;
        border: 1rem;
        border: 1px solid $color-gray;
        cursor: pointer;

        //background-color: $color-white;

        @include flex(column, stretch, flex-start);
    }

    &__item-inner:hover &__pic {
        transform: scale(1.1);
    }

    &__img {
        position: relative;
        overflow: hidden;
    }

    &__pic {
        width: 100%;
        height: auto;
        display: block;
        transition: all .2s ease-in;
    }

    &__info {
        padding: 1rem 1.2rem;
        text-align: center;
        flex: 1 1 auto;
    }

    &__text {
        @include adaptiv-font(16, 14);

        line-height: 2rem;
    }

    &__btn {
        text-align: center;
    }

    &__link:link,
    &__link:visited {
        align-self: center;
        display: inline-block;
        text-align: none;

        @include adaptiv-font(16, 14);
    }
} 
